.page {
  &::-webkit-scrollbar {
    width: 1.5rem;
  }

  &::-webkit-scrollbar-track {
    background-color: $accent-color;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #2d303a;
    border-radius: 2rem;
    outline: 1px solid $accent-color;
    box-shadow: inset 0 0 10px #000000;
  }
}
